










import { Vue, Component } from 'vue-property-decorator'
import LoginForm from '@/views/index/components/LoginForm.vue'

@Component({ components: { LoginForm } })
export default class extends Vue {
  baseApi = ''
  region = '...'

  async beforeCreate() {
    const r = await fetch('https://tele2.ru')
    this.baseApi = r.url.split('/')[2]
    this.region = this.baseApi.split('.')[0].toUpperCase()
  }
}
