








































import { Vue, Component, Watch } from 'vue-property-decorator'
import MaskedInput from 'vue-masked-input'

@Component({ components: { MaskedInput } })
export default class extends Vue {
  number = ''
  smsSent = false
  code = ''
  loadLogin = false

  get numberIsValid() {
    return /^\d{11}$/.test(this.number)
  }

  async sendSms() {
    this.smsSent = true
    await fetch(
      `https://${(this.$parent as any).baseApi}/api/validation/number/${
        this.number
      }`,
      { method: 'POST', body: JSON.stringify({ sender: 'Tele2' }) }
    )
    this.$nextTick(() => (this.$refs['code-input'] as any).$el.focus())
  }

  @Watch('code')
  codeChange(code: string) {
    if (/^\d{6}$/.test(code)) this.login()
  }

  async login() {
    this.loadLogin = true
    const data = new URLSearchParams()
    data.append('client_id', 'digital-suite-web-app')
    data.append('grant_type', 'password')
    data.append('username', this.number)
    data.append('password', this.code)
    data.append('password_type', 'sms_code')
    const r = await fetch(
      'https://my.tele2.ru/auth/realms/tele2-b2c/protocol/openid-connect/token',
      {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: data
      }
    )
    const rj = await r.json()
    console.log(rj)
    this.loadLogin = false
  }
}
